import styled from 'styled-components';
import { FixWidthFlexContainer, Flex } from '../../theme/grid';
import { media } from '../../theme/media';
import { green, white } from '../../theme/variables';
import { bounce } from '../../theme/doBounce';

export const Image = styled.img`
  width: 100%;
`;

export const ProfileContainer = styled(Flex)`
  height: 13em;
  font-size: 2em;
  ${media.larger`
    height: 12em;
    font-size: 1.75em;  
  `}
  ${media.desktop`
    height: 12em;
    font-size: 1.75em;  
  `}
  ${media.tablet`
    height: 11em;
    font-size: 1.5em;  
  `}
  ${media.phone`
    height: 11em;
    font-size: 1em;  
  `}
  
  background-color: ${green};
  background-size: cover;
  background-position: center;
  
  color: ${white};

  h1 {
    margin: 0;
  }
  h3 {
    margin-top: 0;
  }
`;

export const ProfileImageContainer = styled(FixWidthFlexContainer)`
    animation: ${bounce} 1.4s ease-out 0.7s;
`;

export const ProfileImage = styled.img`
  display: block;
  border-radius: 50%;
  height: 300px;
  width: 300px;
  margin: 1em;
  ${media.larger`
    height: 250px;
    width: 250px;
  `}
  ${media.desktop`
    height: 220px;
    width: 220px;
  `}
  ${media.tablet`
    height: 170px;
    width: 170px;
  `}
  ${media.phone`
    height: 150px;
    width: 150px;
  `}
`;



